import { useEffect, useState } from 'react';
import { Helmet } from 'react-helmet';
import { isDesktop } from 'react-device-detect';
import { arrayPaginate } from 'helpers';
import { titlePageGenerator } from 'utils';
import Button from 'components/atoms/Button';
import Header from 'components/molecules/Header/Header';
import { ICompanyDetail } from 'interfaces';
import CompanyLogoNameCard from 'components/molecules/CompanyLogoNameCard/CompanyLogoNameCard';
import { useNavigate } from 'react-router-dom';
import Gap from 'components/atoms/Gap/Gap';

export default function ExpoMobilePage({ source, title }: { source: ICompanyDetail[]; title: string }) {
  const [page, setPage] = useState(1);
  const navigate = useNavigate();
  const data = [...source];

  const paginateData: ICompanyDetail[] = arrayPaginate(data, 10, page);
  const maxPage = Math.ceil(data.length / 10);

  const handlePrev = () => {
    if (page === 1) return;
    window.scroll({
      behavior: 'smooth',
      top: 0,
    });
    setPage(page - 1);
  };

  const handleNext = () => {
    if (page === maxPage) return;
    window.scroll({
      behavior: 'smooth',
      top: 0,
    });
    setPage(page + 1);
  };

  const handleChangePage = (choosenPage: number) => {
    setPage(choosenPage);
    window.scroll({
      behavior: 'smooth',
      top: 0,
    });
  };

  useEffect(() => {
    if (isDesktop) {
      navigate('/expo-hall');
    }
  }, []);

  return (
    <>
      <Helmet>
        <title>{titlePageGenerator(`${title}`)}</title>
      </Helmet>
      <div className="screenWrapper pb-5">
        <Header />
        <div className="py-3">
          <h2 className="mb-2 text-center text-2xl font-bold uppercase">{title}</h2>
        </div>
        <div className="mb-3 grid grid-cols-2 gap-3 px-3">
          {paginateData.map((item) => {
            if (item.id === 'blankCompany') return null;
            return (
              <CompanyLogoNameCard
                key={`${item.id}${Math.random()}`}
                id={item.id}
                image={item.image}
                title={item.name}
              />
            );
          })}
        </div>
        <Gap className="h-12" />
        <div className="fixed inset-x-0 bottom-0">
          <div className="flex justify-center gap-3 bg-white py-2 shadow-lg">
            <Button display="inline-block" onClick={handlePrev} className="text-white" disabled={page === 1}>
              Prev
            </Button>
            {Array(maxPage)
              .fill(0)
              .map((_, index) => (
                <Button
                  key={Math.random()}
                  display="inline-block"
                  onClick={() => handleChangePage(index + 1)}
                  className="text-white"
                  disabled={page === index + 1}
                >
                  {index + 1}
                </Button>
              ))}
            <Button
              display="inline-block"
              onClick={handleNext}
              className="text-white"
              disabled={page === maxPage}
            >
              Next
            </Button>
          </div>
        </div>
      </div>
    </>
  );
}
