import LinkTo from 'components/atoms/LinkTo/LinkTo';

interface Props {
  image: string;
  title: string;
  id: string;
}

export default function CompanyLogoNameCard(props: Props) {
  const { image, title, id } = props;
  return (
    <LinkTo href={`/booth/${id}`} className="block rounded-xl bg-white">
      <img src={image} alt={title} className="block aspect-square w-full object-contain" />
      <div className="p-1">
        <p className="text-center font-bold text-primary">{title}</p>
      </div>
    </LinkTo>
  );
}
